	module.exports = {"data":{"markdownRemark":{"html":"<p>新闻系要写我的学生来找我，问一些关于我自己的问题。因为是市高教局的指令，不能不接待。她问了我说的关于\"复旦情结\"的意思我说我没有那样的诗情画意，我就是觉得复旦这里有我不能离开的生命圈，也许就在复旦这红色的基调里，也许就在这些有生命运动的朋友关系中，也许就在这一片蓝天中。人的生命总是要有寄托的，在失去了父母之后，你会突然觉得这样的寄托是重要的。没有子女，没有家庭的温暖，唯一的寄托就是生命消耗在那里的地方。可能世界上有很多人是这样的。这就是生命的结构。</p>","id":"/home/swain/workspace/political-life/src/pages/zh/0065.md absPath of file >>> MarkdownRemark","fileAbsolutePath":"/home/swain/workspace/political-life/src/pages/zh/0065.md","frontmatter":{"title":"4月2日 星期六"}}},"pathContext":{"id":"/home/swain/workspace/political-life/src/pages/zh/0065.md absPath of file >>> MarkdownRemark"}}

